@import url(https://fonts.googleapis.com/css2?family=Walter+Turncoat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background-color: #252424;
  text-align: center;
}

.App-header {
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Walter Turncoat', cursive;
  font-size: calc(10px + 2vmin);
  color: #e7e247;
  margin-bottom: 2em;
}

h1 {
  background-color: #ef2d55;
  border-radius: 10px;
  padding: 0.5em 1.25em;
}

button.reset {
  background-color: #252424;
  border: 1px solid #ef2d55;
  border-radius: 5px;
  color: #ef2d55;
  padding: 0.5em;
  margin: 1em;
  text-align: center;
  font-size: calc(1px + 2vmin);
}

button.reset:hover {
  background-color: #ef2d55;
  color: #252424;
}

div.grid {
    width: 600px;
    height: 600px;
    margin: 0 auto;
    background-color: #e7e247;
    color: #fff;
    border: 6px solid #e7e247;
    border-radius: 10px;
    display: grid;
    grid-template: repeat(3, 1fr) / repeat(3, 1fr);
}

.square
{
  border: 4px solid #e7e247;
  border-radius: 2px;
  color: #252424;
  font-family: 'Walter Turncoat', cursive;
  font-weight: bold;
  font-size: 8em;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2px;
}

.square:hover { 
  background-color: #e7e247;
}
